.gear-container{
    position: relative;
    top: -15rem;
    border: .1rem transparent solid;
    border-radius: 6px;
    width: 15rem;
    height: 100%;
}
.black-line{
    width: 100%;
    border: .25rem solid black;
    filter: drop-shadow(0 0 0.75rem black);
}
.red-line{
    position: relative;
    left: 50%;
    transform: translate(-50%);
    width: 85%;
    border: .25rem solid red;
    filter: drop-shadow(0 0 0.75rem red);
}
.yellow-line{
    position: relative;
    left: 50%;
    transform: translate(-50%);
    width: 75%;
    border: .25rem solid #f9d70b;
    filter: drop-shadow(0 0 0.75rem #f9d70b);
}
#big-gear{
    position: relative;
    top: -2.25rem;
    width: 8rem;
    height: auto;
    margin-left: 1.9rem;
    filter: drop-shadow(0 0 0.25rem black);
}
#small-gear{
    position: relative;
    z-index: 20;
    width: 5rem;
    height: auto;
    margin-top: 2rem;
    filter: drop-shadow(0 0 0.25rem black);
}
#small-gear.rotate {
    animation: rotate 1.75s infinite linear;
    animation-name: rotate, small-gear;
}
#big-gear.rotate{
    animation: rotate 3s infinite linear;
    animation-name: rotate, big-gear;
}
@keyframes small-gear {
    100% {
        transform: rotate(-360deg);
    }
}
@keyframes big-gear {
    100% {
        transform: rotate(360deg);
    }
}