.gears{
    position: relative;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    width: 15rem;
    font-family: 'Acme', sans-serif;   
}
.step{
    text-align: center;
    color: white;
    background-color: black;
    border: 0 solid;
    border-radius: 6px;
    margin: 3rem 0 3rem 0;
    width: 25rem;
    padding: 3rem 0 3rem 0;
}