.final-pitch{
    background-color: #f9d70b;
    background-image: linear-gradient(to top right, #f9d70b, #fdf2b4);
    color: white;
    text-shadow: .1rem .1rem black;
    border: 0px;
    border-radius: 6px;
    width: 25rem;
    height: auto;
    margin-bottom: 3rem;
    padding: .75rem 3rem .75rem 3rem;
    filter: drop-shadow(0 0 0.5rem rgb(251, 229, 106, 0.5));
    font-family: 'Roboto', sans-serif;
    position: relative;
    left: 50%;
    transform: translate(-50%);
}
.final-pitch:hover{
    filter: drop-shadow(0 0 0.75rem rgb(250, 221, 56, 0.75));
}
@media only screen and (max-width: 992px) {
    .final-pitch{
        display: none;
    }
}
.tagline{
    text-align: center;
    font-family: 'Acme', sans-serif; 
    padding: 3rem 0 3rem 0;
}