.pitch-button{
    background-color: #f9d70b;
    background-image: linear-gradient(to top right, #f9d70b, #fbe56a);
    color: white;
    text-shadow: .1rem .1rem #000000;
    border: 0px;
    border-radius: 6px;
    width: 25rem;
    margin-bottom: 3rem;
    height: auto;
    padding: .75rem 3rem .75rem 3rem;
    filter: drop-shadow(0 0 0.5rem rgb(251, 229, 106, 0.5));
    font-family: 'Roboto', sans-serif;
    position: relative;
    left: 50%;
    transform: translate(-50%);
}
.pitch-button:hover{
    filter: drop-shadow(0 0 0.75rem rgb(254, 251, 230, 0.5));
}
@media only screen and (max-width: 992px) {
    .parent-row{
        background-attachment: scroll;
    }
    .pitch-button{
        display: none;
    }
}
.parent-row{
    background-color: transparent;
    background-image: url('/public/images/marquee-background-cropped.png');
    height: 100%;
    background-attachment: fixed;
    background-position: left top;
    background-repeat: no-repeat;
    background-size: cover;
}
.pitch{
    font-family: 'Acme', sans-serif;
    color: black;
    padding: 6rem 3rem 10rem 3rem;
}