input{
    width: 100%;
    border: .2rem solid black;
    border-radius: 6px;
    box-shadow: 0px 0px .2rem inset;
}
input:focus{
    background-color: #fff6cc;
    outline: none;
}
input[type=message]{
    height: 5rem;
    padding: .5rem;
    box-sizing: border-box;
    resize: none;
}
#error{
    color: red;
}
.form-container{
    position: relative;
    left: 50%;
    transform: translate(-50%);
    width: 25em;
    border: 1rem solid black;
    border-radius: 6px;
    font-family: 'Acme', sans-serif;
    filter: drop-shadow(0 0 0.25rem black);
    margin: 3rem 0 5rem 0;
}
#contact-label{
    height: 2rem;
}
.contact-header{
    text-align: center; 
    padding: 3rem 0 3rem 0;
    width: 100%;
    background-color: black;
    color: white;
    margin: 0;
}
.contact-inner{
    position: relative;
    left: 50%;
    transform: translate(-50%);
    background-color: #fffbe6;
    padding: 1rem;
    box-shadow: 0px 0px 0.75rem inset;
}
.contact-submit{
    width: 100%;
    background-color: #990000;
    color: white;
    padding: 1rem 0 1rem 0;
    text-align: center;
    border: 2px solid white;
}
.contact-submit:hover{
    background-color: #cc0000;
}
.success-container{
    height: 42rem;
    text-align: center;
}
.success-message{
    position: relative;
    top: 50%;
    transform: translate(0,-50%)
}
.error-container{
    height: 42rem;
    text-align: center;
}
.error-message{
    position: relative;
    top: 50%;
    transform: translate(0,-50%)
}