.dw-container{
    font-family: 'Acme', sans-serif;
}
.dw-section-two{
    margin: 5rem 3rem;
}
.dw-section-three{
    margin: 3rem;
}
.dw-section-four{
    margin-top: 5rem;
}
.dw-section-five{
    position: relative;
    width: 75%;
    left: 50%;
    transform: translate(-50%);
    padding-bottom: 3rem;
}
.dw-section-six{
    position: relative;
    left: 50%;
    transform: translate(-50%);
    width: 65%;
    margin-bottom: 10rem;
}
.dw-section-ten{
    text-align: center;
    margin: 3rem;
}
.twenty-five{
    text-underline-offset: .25rem;
    text-decoration: none;
}
.two-eighty-nine{
    text-underline-offset: .25rem;
    text-decoration: none;
}
.stripe{
    z-index: -10;
    width: 100%;
    height: 12rem;
    background-color: red;
    background-image: linear-gradient(to right, red, #ffe6e6);
}
.stripe2{
    z-index: -10;
    width: 100%;
    height: 12rem;
    background-color: #ffe866;
    background-image: linear-gradient(to right, #ffe866, #fffee6);
}
.top-level{
    position: relative;
    top: -17rem;
}
.top-level2{
    position: relative;
    top: -17rem;
}
.concierge-message{
    padding-top: 6rem;
}
.vip-message{
    padding-top: 6rem;
}
.check-mark{
    width: 3.5rem;
    height: auto;
}
.seven-ten{
    margin-bottom: 1rem;
}
.combo-container{
    width: 100%;
    background-color: #fffbe6;
    text-align: center;
    padding: 3rem;
}
.combo{
    color: red;
    text-underline-offset: .25rem;
    text-decoration: none;
}
.closer-button{
    background-color: #f9d70b;
    background-image: linear-gradient(to top right, #f9d70b, #fdf2b4);
    color: white;
    text-shadow: .1rem .1rem black;
    border: 0px;
    border-radius: 6px;
    width: 25rem;
    margin-bottom: 3rem;
    height: auto;
    padding: .75rem 3rem .75rem 3rem;
    filter: drop-shadow(0 0 0.5rem rgb(251, 229, 106, 0.5));
    font-family: 'Roboto', sans-serif;
}
.closer-button:hover{
    filter: drop-shadow(0 0 0.75rem rgb(250, 221, 56, 0.75));
}

@media only screen and (max-width: 992px) {
    .stripe-image{
        display: none;
    }
    .stripe-image2{
        display: none;
    }
    .check-card-container{
        text-align: center;
        padding-bottom: 1rem;
    }
    .dw-section-six{
        width: 90%;
    }
    .dw-section-eight{
        text-align: center;
    }
    .check-mark{
        padding-bottom: 1rem;
    }
    .closer-button{
        display: none;
    }
}