.logo{
    width: 15rem;
    margin-left: 5rem;
    filter: drop-shadow(0 0 0.25rem rgb(0,0,0,0.25));
}
.consultation{
    z-index: 20;
    background-color: #f9d70b;
    background-image: linear-gradient(90deg, #f9d70b, #fcee9c);
    color: white;
    text-shadow: .1rem .1rem #000000;
    border: 0px;
    border-radius: 6px;
    width: 15rem;
    height: auto;
    margin-right: 7rem;
    padding: .5rem 3rem .5rem 3rem;
    filter: drop-shadow(0 0 0.5rem rgb(251, 229, 106, 0.5));
    font-family: 'Roboto', sans-serif;
}
.consultation:hover{
    margin-top: 1rem;
}
#link{
    color: black;
}
#link:hover{
    color: orangered;
}
.dropdown:hover .dropdown-menu {
    display: block;
    margin-top: 0; 
    z-index: 20
 }
@media only screen and (max-width: 992px) {
    .consultation {
        position: fixed;
        bottom: 1rem;
        left: 50%;
        transform: translate(-50%);
    }
    .consultation:hover{
        bottom: 0.5rem;
    }
}