#tri-fold{
    padding: 0 .1rem 0 .1rem;
    font-family: 'Acme', sans-serif;
    height: auto;
    width: 100%;
    text-align: center;
}
#tri-fold-label-1{
    padding: 1.5rem 0 1.5rem 0;
    color: white;
    text-shadow: .1rem .1rem black;
    background-color: red;
    background-image: linear-gradient(to top, red, #ff9999);
}
#tri-fold-label-2{
    background-color: black;
    padding: 1.5rem 0 1.5rem 0;
    color: white;
    text-shadow: .1rem .1rem black;
    background-image: linear-gradient(to top, black, #666666);
}
#tri-fold-label-3{
    background-color: #f9d70b;
    background-image: linear-gradient(to top, #f9d70b, #fdf2b4);
    color: white;
    text-shadow: .1rem .1rem black;
    padding: 1.5rem 0 1.5rem 0;
}
.tri-fold-bottom{
    color: black;
    padding: 2rem 3rem 2rem 3rem;
}