#footer-container{
    background-color: red;
    background-image: linear-gradient(to top, red, #ff9999);
    color: white;
    font-family: 'Acme', sans-serif;
    padding-bottom: 5rem;
}
.footer-logo{
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 10rem;
    padding-bottom: 2rem;
}
.footer-nav{
    color: white;
}
.footer-nav:hover{
    color: black;
}